<script lang="ts">
import { defineComponent, reactive, toRefs, ref, computed, watch, PropType } from 'vue';
import VueNextSelect from 'vue-next-select'
import IconArrow from 'assets/svg/arrow.svg?raw';

import 'vue-next-select/dist/index.css'

export interface OptionSelect {
  id?: string | number;
  title?: string;
  label?: string;
  value?: string | number | Array<string | number>;
  [x: string | number | symbol]: unknown;
}

interface Data {
  internalValue?: any,
}

const KoddeinFormSelect = defineComponent({
  name: "KoddeinFormSelect",
  components: {
    'vue-select': VueNextSelect,
  },
  props: {  
    id: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      default: undefined,
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    /** input value */
    value: {
      type: [String, Array, Object, Number],
      default: undefined,
    },
    /** input options - select */
    options: {
      type: Array as PropType<OptionSelect[]>,
      default: () => [],
    },
  },
  emits: ['inputChange', 'onFocus', 'onBlur'],
  setup(props, { emit }){ 
    const select = ref<any>()
    const data:Data = reactive(
      {
        /** @param {any} internalValue - clone value
         * - because we must not change props
         * - in `watch` will sync value within internalValue
         */
        internalValue: props.multiple ? props.value || [] : props.value || null,
      }
    );

    /**
     * Watch value, on value update -> set internal value
     */
     watch(
      ()=>props.value,
      (value) => { 
        if (value) {
          data.internalValue = value;
        } else {
          data.internalValue = props.multiple ? [] : null;
        }
      }
    )

    /**
     * Watch internal value, on value update -> send value to parent
     */
    watch(
      ()=>data.internalValue,
      (value) => { 
        if (value === props.value) return;
        emit('inputChange', value);
      },
      // { immediate:true }
    )

    const focus = () => {
      emit('onFocus');
    }
    const blur = () => {
      emit('onBlur');
    }

    const computedLabelBy = computed(() => {
      if (props.options[0].label) {
        return 'label'
      }
      return 'title'
    })
    const computedValueBy = computed(() => {
      if (props.options[0].value) {
        return 'value'
      }
      return 'id'
    })

    const isSelected = computed(() => {
      if (props.multiple) {
        return data.internalValue?.length > 0
      }
      return !!data.internalValue
    })

    const testEscape = () => {
      select.value?.blur();
    }

    return {
      ...toRefs(data), //data
      select,
      IconArrow,
      isFocus: focus,
      isBlur: blur,
      isSelected,
      computedLabelBy,
      computedValueBy,
      testEscape,
    }; 
  },
});
export default KoddeinFormSelect
</script>

<template>
  <div class="select" :data-selected="isSelected">
    <vue-select ref="select"
                v-model="internalValue"
                class="input" 
                :options="options" 
                :label-by="computedLabelBy"
                :value-by="computedValueBy"
                :placeholder="placeholder"
                :multiple="multiple"
                :close-on-select="multiple ? false : true"
                open-direction="bottom"
                :max-height="230"
                data-lenis-prevent
                @keyup.esc.exact="testEscape"
                @focus="isFocus"
                @blur="isBlur"
    >
      <!-- Label template with selectde handle -->
      <!-- <template #label="{ selected }">
        <template v-if="selected && selected.constructor === Array && selected.length">
          {{ placeholder }} ({{ selected.length }})
        </template>
        <template v-else-if="selected && selected.constructor !== Array">
          {{ selected.label }}
        </template>
        <template v-else>
          {{ placeholder }}
        </template>
      </template> -->
      <template #toggle="{ isFocusing }">
        <span :class="`arrow ${isFocusing ? '-open' : ''}`"
              v-html="IconArrow"
        />
      </template>
      <!-- Template for dropdown item -->
      <!-- <template #dropdown-item="{ option }">
        <div :class="`${multiple ? '-multiple' : ''}`">
          {{ option.label || option.title }}
          <span v-if="multiple"
                class="icon-inactive"
                v-html="IconArrow"
          />
          <span v-if="multiple"
                class="icon-active"
                v-html="IconArrow"
          />
        </div>
      </template> -->
    </vue-select>
  </div>
</template>

